import classes from './ProfileInfo.module.css'
import banner from '../../../img/profilebanner.png'
import avatar from './../.././../img/avatar.jpg'
import upload from './../../../img/upload.png'
import Preloader from '../../common/preloader/Preloader'
import ProfileStatus from './ProfileStatus'
import { useDispatch, useSelector } from 'react-redux'
import { getFromAuth } from '../../../Redux/selectors/profile-selectors'
import { savePhoto } from '../../../Redux/profileReducer'

const ProfileInfo = (props) => {
    const login = useSelector(state => getFromAuth.login(state))
    const dispatch = useDispatch()
    if (!props.profile){
        return <Preloader/>
    }
    const onPhotoSelected = (e) => {
        if (e.target.files.length){
            dispatch(savePhoto(e.target.files[0]))
        }
    }

    return (
        <div className={ classes.profileinfo }>

            <div className={ classes.descriptionBlock }>
                <div>
                    <img className={ classes.bannerImg } src={ banner } alt="banner"/>
                </div>
                <p className={ classes.name }>{ props.profile.fullName }</p>
                <div className={ classes.avatarWrapper }>
                    <img className={ classes.avatar } src={ props.profile.photos.large ?? avatar }/>
                    { login === props.profile.fullName
                    && (<input
                            id="input__file"
                            className={ classes.inputFile }
                            type="file"
                            onChange={ onPhotoSelected }/>) }
                    <label htmlFor="input__file" >
                        <span title='Выберите аватарку' className={classes.iconWrapper}><img className={classes.inputFileIcon}
                                                                   src={upload}
                                                                   alt="Выбрать файл"/></span>
                    </label>
                </div>
                <ProfileStatus/>
                <div>
                    <p>Ищу работу: { props.profile.lookingForAJob
                        ? <span>да</span>
                        : <span>нет</span> }
                    </p>
                    <p>Навыки: { props.profile.lookingForAJobDescription ?? 'Не указаны'
                    }</p>
                    <p>Контакты:</p>
                    { props.profile.contacts
                        ? <ul>
                            <li>Github: { props.profile.contacts.github ?? <span>нет</span> }
                            </li>
                            <li>VK: { props.profile.contacts.vk ?? <span>нет</span> }
                            </li>
                            <li>Facebook: { props.profile.contacts.facebook ?? <span>нет</span> }
                            </li>
                            <li>Instagram: { props.profile.contacts.instagram ?? <span>нет</span> }
                            </li>
                            <li>Twitter: { props.profile.contacts.twitter ?? <span>нет</span> }
                            </li>
                            <li>Website: { props.profile.contacts.website ?? <span>нет</span> }
                            </li>
                            <li>Youtube: { props.profile.contacts.youtube ?? <span>нет</span> }
                            </li>
                            <li>Mail: { props.profile.contacts.mailLink ?? <span>нет</span> }
                            </li>
                        </ul>
                        : 'Не удалось получить контактную информацию :-('
                    }
                </div>
            </div>
        </div>
    )
}

export default ProfileInfo