import Profile from './Profile'
import { connect, useDispatch } from 'react-redux'
import { getUserProfile, getUserStatus } from '../../Redux/profileReducer'
import { withAuthRedirect } from '../../HOC/WithAuthRedirect'
import { useRouteMatch } from 'react-router-dom'
import { compose } from 'redux'
import * as React from 'react'
import { useEffect } from 'react'
import { getFromAuth, getFromProfile } from '../../Redux/selectors/profile-selectors'


const ProfileContainer = (props) => {
    const match = useRouteMatch()
    let userId = match.params.userId
    if (!userId){
        userId = props.authorisedUserId
    }
    const dispatch = useDispatch()

    useEffect(() => {

        dispatch(getUserProfile(userId))
        dispatch(getUserStatus(userId))
    }, [dispatch, userId])
    return (
        <Profile
            { ...props }
        />
    )


}

let mapStateToProps = (state) => {
    return {
        profilePage: getFromProfile.profilePage(state),
        authorisedUserId: getFromAuth.authUserId(state),
        isAuth: getFromAuth.isAuth(state),

    }
}

export default compose(
    connect(mapStateToProps),
    withAuthRedirect
)(ProfileContainer)
