import * as React from 'react'
import { updateUserStatus } from '../../../Redux/profileReducer'
import { useDispatch, useSelector } from 'react-redux'
import { getFromProfile } from '../../../Redux/selectors/profile-selectors'
import { useEffect, useState } from 'react'

const ProfileStatus = () => {
    const dispatch = useDispatch()
    const statusFromProps = useSelector(state => getFromProfile.status(state))
    const [editMode, setEditMode] = useState(false)
    const [status, setStatus] = useState(statusFromProps)


    useEffect(() => {
        setStatus(statusFromProps)
    }, [statusFromProps])

    const activateEditMode = () => {
        setEditMode(true)
    }
    const deactivateEditMode = () => {
        setEditMode(false)
        dispatch(updateUserStatus(status))
    }
    const onStatusChange = (e) => {
        setStatus(e.currentTarget.value)
    }

    return (
        <div>
            { !editMode
                ? <div><span onDoubleClick={ activateEditMode }>
                        { statusFromProps !== ''
                            ? statusFromProps
                            : 'Дважды кликните, чтобы установить статус' }
                    </span></div>
                : <div>
                    <input onChange={ onStatusChange } autoFocus={ true } value={ status }
                           onBlur={ deactivateEditMode }/>
                </div>
            }
        </div>
    )
}

export default ProfileStatus